<template>
	<header v-if="getHeader">
		<a v-if="backType" @click="aBtn()">
			<i class="iconfont icon-fanhui"></i>
		</a>
		<span v-text="pageName"></span>
		<a class="bj" v-if="clickBtn" @click="clickAction" v-html="clickBtn">
		</a>
	</header>
</template>

<script>
	import Env from '../../config/env.js';
	export default {
		props: ['pageName', 'backAction', "backType", "goback", "clickBtn", "clickAction"],
		computed: {
			getHeader: function() {
				if(localStorage.android == "true") {
					return false;
				} else if(localStorage.ios == "true") {
					return false;
				} else {
					return true;
				}
			}

		},
		methods: {
			aBtn: function() {
				if(this.backAction != undefined) {
					this.backAction();
				} else {
					let _back = this.$route.query.backUrl
					console.log(_back)
					if(_back) {
						this.$route.router.go({
							path: _back,
						});
					} else {
						history.go(-1);
					}
				}
			}
		},

	}
</script>
<style lang="sass" scoped>
	/*@import url("../assets/iconfonts/iconfont.css");*/
</style>


