<template>
	<div class="calendar-list">
		<ul class="week">
			<li>一</li>
			<li>二</li>
			<li>三</li>
			<li>四</li>
			<li>五</li>
			<li>六</li>
			<li>日</li>
		</ul>
		<div class="calendar">
			<div class="calendar-item" v-for="date in dateArr">
				<div class="calendar-header"><span>{{date.y}}年{{date.m}}月</span></div>
				<div class="calendar-body">
					<ul class="days">
						<li v-for="day in date.days">
							<template v-if="day.getMonth()+1 != date.m">
								<span></span>
							</template>
							<template v-else>
								<template v-if="isToday(day)">
									<span @click="select(day)">{{ '今天' }}</span>
								</template>
								<template v-else>
									<span v-if="day<new Date()" class="disabled">{{ day.getDate() }}</span>
									<span v-else @click="select(day)">{{ day.getDate() }}</span>
								</template>
							</template>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
				return {
					aroud: 14,
					currentDate: {
						currentDay: null,
						currentMonth: null,
						currentYear: null,
						currentWeek: null
					},
					dateArr: []
				}
			},
			props: [],
			computed: {},
			created: function() {
				for(var x = 0; x < this.aroud; x++) {
					var days = this.calendarInit(this.currentDate.currentYear, this.currentDate.currentMonth);
					var timeObj = {
						y: this.currentDate.currentYear,
						m: this.currentDate.currentMonth,
						days: days
					};
					this.dateArr.push(timeObj);
				}
			},
			methods: {
				isToday: function(day) {
					return day.getFullYear() == new Date().getFullYear() && day.getMonth() == new Date().getMonth() && day.getDate() == new Date().getDate() ? true : false;
				},
				getDay: function(date) {
					this.currentDate.currentDay = date.getDate();
					this.currentDate.currentYear = date.getFullYear();
					this.currentDate.currentMonth = date.getMonth() + 1;
					this.currentDate.currentWeek = date.getDay();
					if(this.currentDate.currentWeek == 0) {
						this.currentDate.currentWeek = 7;
					}
				},
				formatDate: function(year, month, day) {
					var y = year;
					var m = month;
					if(m < 10) m = "0" + m;
					var d = day;
					if(d < 10) d = "0" + d;
					return y + "-" + m + "-" + d
				},
				calendarInit: function(year, month) {
					var date, d;
					var days = [];
					if(year == null || month == null) {
						var _date = new Date();
						d = new Date(_date.getFullYear(), _date.getMonth() - 1, 1);
					} else {
						d = new Date(year, month - 1, 1);
					}
					d.setDate(42);
					date = new Date(d.getFullYear(), d.getMonth(), 1);
					this.getDay(date);
					for(var i = this.currentDate.currentWeek - 1; i >= 0; i--) {
						var d = new Date(this.currentDate.currentYear, this.currentDate.currentMonth - 1, this.currentDate.currentDay);
						d.setDate(d.getDate() - i);
						days.push(d);
					}
					for(var i = 1; i <= 42 - this.currentDate.currentWeek; i++) {
						var d = new Date(this.currentDate.currentYear, this.currentDate.currentMonth - 1, this.currentDate.currentDay);
						d.setDate(d.getDate() + i);
						days.push(d);
					}
					return days;
				},
				select: function(date) {
					alert(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()));
				},
			},

	}
</script>
<style lang="sass" scoped>
	.calendar-list {
		background-color: #fff;
	}
	
	.calendar {
		padding-top: 0.27rem;
	}
	
	.week {
		position: fixed;
		z-index: 50;
		height: 0.27rem;
		width: 100%;
		background: #F9F8F8;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		li {
			text-align: center;
			height: 0.26rem;
			line-height: 0.26rem;
			vertical-align: middle;
			font-size: 0.14rem;
			font-weight: medium;
		}
	}
	
	.calendar-item {
		border-bottom: 1px solid #ddd;
		padding-bottom: 0.1rem;
		.calendar-header {
			text-align: center;
			font-size: 0.17rem;
			font-weight: medium;
			color: #222;
			padding: 0.15rem 0 0.115rem 0;
		}
		.calendar-body {
			ul {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-around;
				li {
					text-align: center;
					height: 0.55rem;
					line-height: 0.55rem;
					vertical-align: middle;
					border: none;
					font-weight: medium;
					color: #222;
					width: 14.28%;
					background-color: #fff;
					position: relative;
				}
				span[class=disabled] {
					color: #999;
				}
			}
		}
	}
</style>


